@media screen and (min-width: 1024px) {

    .site-content {
        /* background-color: lightgreen; */

        display: flex;
        max-width: 90vw;
        margin: 0 auto;

        justify-content: space-between;
    }

    .main-content {
        /* background-color: lightseagreen; */

        display: flex;
        flex-direction: column;
        max-width: 65vw;
    }

    .aside-content {
        /* background-color: lightyellow; */

        max-width: 20vw;
        margin: 0 auto;

        padding-left: 10px;
        padding-right: 10px;

        margin-bottom: 20px;
    }

    .aside-content .content {
        display: flex;
        flex-direction: column;
        row-gap: 30px;
    }

    .publications {
        display: flex;
        flex-direction: column;
        justify-content: stretch;

        padding-top: 20px;
        padding-bottom: 20px;
    }

    .publications-loading {
        padding: 20px;
        text-align: center;
    }

    .markdown-content {
        /* background-color: lightgreen; */

        display: flex;
        flex-direction: column;
        justify-content: stretch;
    }

    .contact-us {
        display: flex;
        flex-direction: column;
        justify-content: stretch;

        padding: 0 10px 0 10px;
        text-align: center;
    }

    .contact-us>form {
        display: flex;
        flex-direction: column;
        justify-content: stretch;

        row-gap: 10px;
    }

    .circular-progress {
        padding: 20px;
        text-align: center;
    }

    .publication-content {
        /* background-color: lightblue; */
        padding: 4vh 4vw 0 2vw;
    }

    .summary-content .date,
    .publication-content .date {
        margin: 0;
        text-align: right;
        color: #666;
    }

    .informations {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 4px;
    }

    @keyframes notif-animation {
        0% {
            transform: rotate(10deg);
        }

        50% {
            transform: rotateZ(-10deg);
        }

        100% {
            transform: rotate(10deg);
        }
    }

    .notif-icon>button {
        transform-origin: top center;
        transform: rotate(0deg);
    }

    .notif-icon>.animate {
        animation: notif-animation .5s infinite alternate;
    }

    .more-publications {
        text-align: center;
    }

    blockquote {
        padding: 1px 10px;
        border-left: 2px solid #ccc;
        background-color: #eceaea;
    }

    .faq-section {
        display: flex;
        flex-direction: column;
        justify-content: stretch;
    }

    .faq-section ul {
        padding: 0;
    }

    .faq-section li {
        display: inline-block;
        background-color: #55befc;
        max-width: 80vw;
        padding: 4px;
        border-radius: 10px;
    }

    .faq-section ul::before {
        display: inline-block;
        background-image: url(https://www.svgrepo.com/show/239140/chat-conversation.svg);
        background-repeat: no-repeat;
        background-size: 24px;
        width: 24px;
        height: 24px;
        content: '';
        margin-left: 10px;
    }

    .faq-section blockquote {
        display: flex;
        flex-direction: row;
        justify-content: end;
        border: none;
        margin: 0;
    }

    .faq-section blockquote::after {
        display: inline-block;
        background-image: url(https://www.svgrepo.com/show/239142/chat-conversation.svg);
        background-repeat: no-repeat;
        background-size: 24px;
        width: 24px;
        height: 24px;
        content: '';
        padding-right: 20px;
    }

    .faq-section blockquote>p {
        background-color: rgb(176, 248, 176);
        margin: 0;
        padding: 10px;
        border-radius: 10px;
        max-width: 80vw;
        margin-right: 4px;
    }

    table {
        width: 65vw;
    }

    th {
        width: 100vw;
        background-color: #b9b9b9;
    }

    tr:nth-of-type(even) {
        background-color: #eceaea;
    }

    .documents-table td:nth-child(1) {
        width: 75%;
    }

}